<template>
  <aside>
    <ul>
      <li v-for="(item, index) in menuList" :key="index">
        <div>{{ item.title }}</div>
        <ul v-if="item.children && item.children.length > 0">
          <li
            v-for="(itemChild, indexChild) in item.children"
            :key="indexChild"
            @click="handleLinkTo(itemChild)">
            {{ itemChild.title }}
          </li>
        </ul>
      </li>
    </ul>
  </aside>
</template>

<script>
import menuList from '@/config/leftMenu'

export default {
  name: 'leftMenu',
  data () {
    return {
      menuList: menuList
    }
  },
  methods: {
    handleLinkTo (item) {
      this.$router.push({
        path: item.router
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  aside {
    min-width: 200px;
    height: 100%;
    overflow-y: auto;
    background-color: aquamarine;
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      li {
        cursor: pointer;
      }
    }
  }
</style>
