import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/about',
      name: 'about',
      component: () => import('./views/About.vue')
    },
    {
      path: '/animationPage',
      name: 'animationPage',
      component: () => import('./views/css/animation.vue')
    },
    {
      path: '/backgroundPage',
      name: 'backgroundPage',
      component: () => import('./views/css/background.vue')
    }
  ]
})
